* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  color: #333;
  -webkit-user-select: none;
  user-select: none;
  -moz-text-size-adjust: 100%;
  text-size-adjust: 100%;
  background-image: url("LSG Background (1).ba538d3e.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Open Sans, sans-serif;
  overflow: hidden;
}

#container {
  position: fixed;
  top: calc(50% + 1.5rem);
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

#topbar {
  text-align: center;
  width: 100%;
  padding: .25rem;
  position: fixed;
  top: 0;
  left: 0;
}

.major_container {
  -webkit-backdrop-filter: blur(2.6px);
  background: #01486838;
  box-shadow: 0 4px 30px #0000001a;
}

.logo {
  z-index: 9999;
  width: 80px;
  height: 50px;
}

#topbar button {
  font: inherit;
  color: #fff;
  cursor: pointer;
  background: none;
  border: 1px solid #fff;
  outline: none;
  margin: .1rem;
  padding: .4rem;
  font-size: .75rem;
  line-height: 1;
}

#topbar button:hover {
  color: #444;
  background-color: #fff;
}

.message {
  text-align: center;
  width: 100%;
  padding: 1rem .5rem;
  font-size: .5rem;
  position: fixed;
  top: 2.5rem;
  left: 0;
}

.card {
  cursor: default;
  will-change: transform;
  background-color: #fff;
  border-radius: 4px;
  width: 3.875rem;
  height: 5.5rem;
  display: inline-block;
  position: absolute;
  top: -2.75rem;
  left: -1.9375rem;
  box-shadow: 0 1px 1px #00000026;
}

.card:before, .card:after {
  text-align: center;
  white-space: pre-line;
  letter-spacing: -.1rem;
  width: .55rem;
  font-family: Ubuntu Condensed, sans-serif;
  font-size: .7rem;
  line-height: .7rem;
  position: absolute;
}

.card:before {
  top: .15rem;
  left: 0;
}

.card:after {
  bottom: .1rem;
  right: 0;
  transform: rotate(180deg);
}

.card .face {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 100%;
}

.card .back {
  background-image: url("Card Vertical Design 4.61641eab.png");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.card.spades, .card.clubs, .card.joker {
  color: #000;
}

.card.hearts, .card.diamonds, .card.joker.rank3 {
  color: #d40000;
}

.card.joker.rank1:before, .card.joker.rank2:before, .card.joker.rank3:before, .card.joker.rank1:after, .card.joker.rank2:after, .card.joker.rank3:after {
  content: "J\a O\a K\a E\a R";
  letter-spacing: 0;
  width: .5rem;
  padding: .15rem .05rem;
  font-size: .4rem;
  line-height: .4rem;
}

